import React, { useEffect, useState, Suspense } from 'react';
import * as draftJs from 'draft-js';
const { EditorState, convertToRaw, convertFromHTML, ContentState } = ((draftJs as any).default ??
    draftJs) as typeof draftJs;

import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import draftToHtml from 'draftjs-to-html';
import toolbar from './toolbar';
import { useDispatch } from 'react-redux';
import type { Post } from '../../../../../server/models/Post';

export { TextEditor };

function TextEditor({ postInfo }: { postInfo?: Post | null }) {
    const dispatch = useDispatch();

    const [isMounted, setIsMounted] = useState<boolean>(false);
    const [Component, setComponent] = useState<React.ComponentType<any> | null>(null);
    const [editorState, setEditorState] = useState<draftJs.EditorState>(EditorState.createEmpty());

    useEffect(() => {
        const loadComponent = async () => {
            const module = await import('react-draft-wysiwyg');
            setComponent(() => module.Editor);
        };

        loadComponent();
        setIsMounted(true);
    }, []);

    useEffect(() => {
        if (postInfo !== null && postInfo !== undefined) {
            /**
             * @convertFromHTML HTML  (2 ) .  ContentBlock   ,   entityMap   
             * @createFromBlockArray ContentBlock   ContentState .
             */
            dispatch({ type: 'postSlice/CONTENT_STATE', CONTENT: postInfo.content });
            const blocksFromHTML = convertFromHTML(postInfo.content); // HTML  
            const state = ContentState.createFromBlockArray(blocksFromHTML.contentBlocks, blocksFromHTML.entityMap); //   ContentState .
            setEditorState(EditorState.createWithContent(state));
        } else {
            setEditorState(EditorState.createEmpty());
        }
    }, [isMounted, postInfo]);

    const handleChange = (newEditorState: draftJs.EditorState) => {
        if (isMounted) {
            setEditorState(newEditorState);
            dispatch({
                type: 'postSlice/CONTENT_STATE',
                CONTENT: draftToHtml(convertToRaw(newEditorState.getCurrentContent())),
            });
        }
    };

    if (!Component) {
        return <Loading />;
    }

    return (
        <Suspense fallback={<Loading />}>
            <Component editorState={editorState} onEditorStateChange={handleChange} toolbar={toolbar} />
        </Suspense>
    );
}

function Loading() {
    return <div>   </div>;
}
